// @flow

import React from 'react'
import styled from 'styled-components'
import {Link} from 'gatsby'
import {Highlight} from 'react-instantsearch-dom'

const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'

type Props = {
  hit: {
    title: string,
    date: string,
    excerpt: string,
    slug: string,
  },
}

const MyArticle = styled.article`
  font-family: ${FONT_NANUM_GOTHIC};
`

const H3 = styled.h3`
  font-family: ${FONT_NANUM_GOTHIC};
`
// margin-bottom: ${rhythm(1 / 4)};

const MyLink = styled(Link)`
  box-shadow: none;
`

const PostReview = (props: Props) => {
  const {hit} = props

  const {date, slug} = hit

  return (
    <MyArticle key={slug}>
      <header>
        <H3>
          <MyLink to={slug}>
            <Highlight tagName="mark" hit={hit} attribute="title" />
          </MyLink>
        </H3>
        <small>{new Date(date).toLocaleDateString()}</small>
      </header>
      <section>
        <p>
          <Highlight tagName="mark" hit={hit} attribute="excerpt" />
        </p>
      </section>
    </MyArticle>
  )
}

export default PostReview
